    import {
        mapGetters
    } from 'vuex'

	import { miniProApi } from '@/utils/mixins.js'
    export default {
		mixins: [ miniProApi ],
        computed: {
			...mapGetters(['forcedLogin', 'hasLogin', 'userName'])
		},
        onLoad() {
			console.log("----",this.hasLogin)
            if (!this.hasLogin) {
                uni.showModal({
                    title: '未登录',
                    content: '您未登录，需要登录后才能继续',
                    /**
                     * 如果需要强制登录，不显示取消按钮
                     */
                    showCancel: !this.forcedLogin,
                    success: (res) => {
                        if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
                            if (this.forcedLogin) {
                                uni.reLaunch({
                                    url: '../login/login'
                                });
                            } else {
                                uni.navigateTo({
                                    url: '../login/login'
                                });
                            }
                        }
                    }
                });
            }else {
				// this.navigatePage("../find/find")
			}
        }
    }
